.navbar{
  background: #1b242f;
  border-bottom: 3px solid #04c2c9;
}

.navLinks{
    min-width: 100px;
}
.navLinks:after {
  display:block;
  content: '';
  border-bottom: solid 2px #e31b6d;  
  transform: scaleX(0);  
  transition: transform 0.2s ease-in;
}
.navLinks:hover:after { transform: scaleX(1); }
.navLinks:hover:after { transform-origin:  0% 50%; }

.nav-link.active{
    color: #e31b6d !important;
}
.scrollTag{
  display: flex;
}
@media only screen and (max-width : 768px){   
  .scrollTag{
    display: block;
  }
} 