#textEducation{
    padding-top: 60px;
    padding-bottom: 50px;
    color:#444649;
    font-weight: 600;
    text-align: center;
}
.straightLine{
    content: ' ';
    height: 100%;
    max-width: 6px;
    margin: 0 auto;
    background-color: #444649;
}
#Education{
    padding-bottom: 50px;
}
#school,#college,#internship{
    margin-bottom: 1rem !important;
    margin-top: 1rem !important;
}
#school .card,#college .card,#internship .card{
    transition: 0.3s ease;
}
#school .card:hover,#college .card:hover,#internship .card:hover{
    box-shadow: 0 5px 10px gray;
}
.circleTimeOuter,.circleTimeInner{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    position: absolute;
    left: 50%;
    top: 25%;
    transform: translate(-50%,-50%);
    background-color: #e31b6d;
}
.circleTimeInner{
    width: 15px;
    height: 15px;
    background-color: white;
}
#pharmeasy{
    width: 75%;
}
@media only screen and (max-width : 600px){
    #pharmeasy{
        width: 100%;
    }
}
#mnit-logo{
    width: 100%;
}
#rchs-logo{
    width: 100%;
    border-radius: 50%;
}
.outer-card-text{
    display: flex;
    justify-content: center;
}
.inner-card-text{
    align-self: center;
}