#Projects{
    background: #f5f5f5;
    padding-bottom: 100px;
}
#textProject{
    padding-top: 60px;
    padding-bottom: 50px;
    color:#444649;
    font-weight: 600;
    text-align: center;
}
.viewProject{
    border:2px solid #e31b6d;
    padding: 10px;
    color:#616161;
    transition: 0.3s ease-in-out;
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translate(-50%,0);
}
.viewProject:hover{
    background-color: #e31b6d;
    color: white;
    border-color: #e31b6d;
}
@media only screen and (max-width : 600px){
    .projectText{
    font-size: 12px;
    }
}
.projectCardHeader{
    background-color: #1b242f !important;
    color: #e31b6d;
    font-weight: bold;
}

