#textContact{
    padding-top: 60px;
    padding-bottom: 10px;
    color:#fff;
    font-weight: 600;
    text-align: center;
}
#textContactSub{
    color: #e31b6d;
    text-align: center;
    padding-bottom: 40px;
}
#Contact{
    background-color: #252934;
}
.inputControl{
    border: none !important;
    border-radius: 0 !important;
    background-color: #1e242c !important;
    color: white !important;
    
}
.inputControl::placeholder{
    color:#777;
}
.inputControl:-webkit-autofill,
.inputControl:-webkit-autofill:hover, 
.inputControl:-webkit-autofill:focus, 
.inputControl:-webkit-autofill:active  {
    -webkit-box-shadow: 0 0 0 30px #1e242c inset !important;
    -webkit-text-fill-color: white !important;
    caret-color: white;
}
#submitContact{
    border-radius: 0 !important;
    background-color: transparent;
    border: 2px solid white;
    padding: 10px 30px;
    color: white;
    font-weight: bold;
    float: right;
    transition: 0.3s ease;
}
#submitContact:hover{
    border-color: #04c2c9;
    background-color: #04c2c9;
}
#msgStatus{
    color: white;
    font-weight: 300;
}
textarea.inputControl{
    min-height: 100px;
    max-height: 600px;
}