footer{
    background-color: #1b242f;
    margin: 0;
}
#reachMe{
    color: #04c2c9;
    padding-top: 60px;
    padding-bottom: 50px;
    font-weight: 600;
    text-align: center;
    margin-bottom: 0;
}
#reachIconsDiv{
    text-align: center;
    padding-bottom: 20px;
}
.reachIcons,.goTopIcon{
    font-size: 28px;
    min-width: 60px;
    color:#e31b6d;
    transition: 0.3s ease;
}
.goTopIcon{
    min-height: 50px;
    min-width: 50px;
    transform: translate(0,-50%);
    border: 1px solid #e31b6d;
    padding: 10px;
}
.reachIcons:hover{
    color: #04c2c9;
}
.goTopIcon:hover{
    color: #04c2c9;
    border-color: #04c2c9;
}
#copyRight{
    text-align: center;
    color: #777;
    margin: 0;
    padding-bottom: 50px;
}
.my-badge{
    background-color: white;
}