#frontPageAbout{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 48px;
    color: white;
    text-align: center;
    font-weight: bold;
}
a{
    text-decoration: none !important;
}
#frontButton{
    font-size: 24px;
    max-width: 200px;
    min-height: 50px;
    color: white;
    text-align: center;
    border: 2px solid white;
    padding: 15px;
    transition: 0.3s ease-in-out;
    animation: createBox 1s;
}
@keyframes createBox {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
#frontButton:hover{
    background-color: #04c2c9;
    border-color: #04c2c9;
}
.pinkColor{
    color: #e31b6d;
}
#myName div{
    display: flex;
}
#About{
    padding-bottom: 50px;
}
#myImage{
 width: 100%;
 padding-top: 20px;
 padding-bottom: 20px;
}
#infoPara{    
    padding: 10px;
    text-align: justify;
}
.my-badge{
    padding: 10px;
}
.LI-badge-container.horizontal.light{
    width: 100% !important;
}
#textAbout{
    padding-top: 60px;
    padding-bottom: 50px;
    color:#444649;
    font-weight: 600;
    text-align: center;
}
#textInfo{
    font-size: 20px;
    padding-bottom: 50px;
}
/* media queries */
@media only screen and (max-width : 600px){
    #frontPageAbout{
        font-size:24px
    }
    #frontButton{
        font-size: 16px;
    }
    #textInfo{
        font-size: 16px;
    }
    .icon-platform{
        width: 50px;
    }
    #github .icon-platform{
        margin-right: 3px;
    }
}
.btn-resume{
    padding: 10px 20px;
    margin:10px 0;
    width: 150px;
    border-radius: 0 !important;
    border-width: 2px !important;
    font-weight: bold !important;
    color:black !important;
    transition: 0.3s ease-in-out !important;
}
.btn-platform{
    width: 100%;
    height: 100%;
    border-radius: 0 !important;
    border-width: 2px !important;
    font-weight: bold !important;
    color:black !important;
    background: transparent !important;
    transition: 0.3s ease-in-out !important;
}
#platformRow{
    padding: 10px;
}
#platformRow .col-lg-4{
    margin-bottom: 10px;
}
.btn-platform:hover,.btn-resume:hover{
    color:white !important;
}
